<template>
    <div class="about">
      <div class="minWidth">
        <all_titel :indexText="indexText"></all_titel>
         <div class="productsTitle" v-if="proTypeShow===41">
           <div class="productsText">
             浴室系列：
           </div>
           <div class="navList">
             <ul>
               <li @click="checkNav(1)" :class="[addClassToBg===1 ? activeClass : '']">陶瓷盆</li>
               <li @click="checkNav(2)" :class="[addClassToBg===2 ? activeClass : '']">蹲便器</li>
               <li @click="checkNav(3)" :class="[addClassToBg===3 ? activeClass : '']">浴室柜</li>
               <li @click="checkNav(4)" :class="[addClassToBg===4 ? activeClass : '']">马桶</li>
             </ul>
           </div>
         </div>
        <div class="productsTitle" v-if="proTypeShow===43">
          <div class="productsText">
            五金系列：
          </div>
          <div class="navList">
            <ul>
              <li @click="checkNav(9)" :class="[addClassToBg===9 ? activeClass : '']">花洒</li>
              <li @click="checkNav(10)" :class="[addClassToBg===10 ? activeClass : '']">龙头</li>
              <li @click="checkNav(12)" :class="[addClassToBg===12 ? activeClass : '']">小五金</li>
            </ul>
          </div>
        </div>
        <div class="productsTitle" v-if="proTypeShow===44">
           <div class="productsText">
             定制系列：
           </div>
           <div class="navList">
             <ul>
               <li @click="checkNav(13)" :class="[addClassToBg===13 ? activeClass : '']">浴室柜定制</li>
               <li @click="checkNav(14)" :class="[addClassToBg===14 ? activeClass : '']">浴室定制</li>
               <li @click="checkNav(15)" :class="[addClassToBg===15 ? activeClass : '']">卫生间定制</li>
             </ul>
           </div>
         </div>
         <div class="productsTitle" v-if="proTypeShow===45">
           <div class="productsText">
             智能系列：
           </div>
           <div class="navList">
             <ul>
               <li @click="checkNav(16)" :class="[addClassToBg===16 ? activeClass : '']">智能马桶</li>
               <li @click="checkNav(17)" :class="[addClassToBg===17 ? activeClass : '']">恒温花洒</li>
               <li @click="checkNav(18)" :class="[addClassToBg===18 ? activeClass : '']">智能马桶盖</li>
             </ul>
           </div>
         </div>
         <div class="productsTitle" v-if="proTypeShow===46">
           <div class="productsText">
             其他：
           </div>
           <div class="navList">
             <ul>
               <li @click="checkNav(19)" :class="[addClassToBg===19 ? activeClass : '']">感应器</li>
               <li @click="checkNav(20)" :class="[addClassToBg===20 ? activeClass : '']">挂墙式</li>
               <li @click="checkNav(21)" :class="[addClassToBg===21 ? activeClass : '']">小便器</li>
             </ul>
           </div>
         </div>
        <div class="proList">
          <ul>
            <li class="list-item" v-for="item in activeData" :key="item.id" @click="jumpToDetails(item.id)">
              <div class="listImg">
                <img :src="item.url" alt="">
              </div>
              <div class="listText">
                {{item.name}}
              </div>
            </li>
          </ul>
        </div>
        <div class="pageBottom">
          <pagePay  @clickPage="clickPage" :nun="num"></pagePay>
        </div>
      </div>
    </div>
</template>

<script>
    import all_titel from '@/components/pageNavigation/pageNavigation'
    import pagePay from '@/components/pagingButton/pagingButton'
    export default {
      name: "aboutIndex",
      components:{
        all_titel,
        pagePay
      },
      props:{
        proList:{
          type:Object
        }
      },
      data(){
          return{
            num:1,
            proTypeShow:41,
            addClassToBg:1,
            activeClass:'baColor',
            productsListData:{},//所以产品
            activeProductList:{},//当前系列
            activeData:[],//当前系列产品
          }
      },
      computed:{
        products_nav(){
          return this.$store.state.products_nav;
        },
        products_page(){
          return this.$store.state.products_page;
        },
        indexText(){
          return this.$store.state.indexText;
        }
      },
      created(){
        this.proTypeShow =this.$store.state.products_nav;
        this.addClassToBg =this.$store.state.products_page;
        this.getProData();
      },
      watch:{
        products_nav(){
          this.proTypeShow =this.$store.state.products_nav;
          this.selectProType();
          this.selectListType();
        },
        products_page(){
          this.addClassToBg =this.$store.state.products_page;
        }
      },
      methods:{
        clickPage(val){
          if(this.num===2){
            if(val===1){
            }else if(val===2){
            }
          }
        },
        getProData(){
           this.productsListData = this.proList.productsData;
           this.selectProType();
           this.selectListType();
        },
        selectProType(){//获取种类
          if(this.proTypeShow===41){
            this.activeProductList = this.productsListData.bathroomData;
          }else if(this.proTypeShow===43){
            this.activeProductList = this.productsListData.hardwareData;
          }else if(this.proTypeShow===44){
            this.activeProductList = this.productsListData.customizedData;
          }else if(this.proTypeShow===45){
            this.activeProductList = this.productsListData.IntelligenceData;
          }else if(this.proTypeShow===46){
            this.activeProductList = this.productsListData.otherData;
          }
        },
        selectListType(){
         let page = this.$store.state.products_page;
         if(page===''){
           this.activeData =  this.activeProductList.ceramicData;
           this.addClassToBg=1;
         }if(page===1){
            this.activeData =  this.activeProductList.ceramicData;
          }else if(page===2){
            this.activeData =  this.activeProductList.squattingData;
          }else if(page===3){
            this.activeData =  this.activeProductList.pedestalData;
          }else if(page===4){
            this.activeData =  this.activeProductList.stoolData;
          }else if(page===9){
            this.activeData =  this.activeProductList.showerData;
          }else if(page===10){
            this.activeData =  this.activeProductList.faucetData;
          }else if(page===12){
            this.activeData =  this.activeProductList.hardwareData;
          }else if(page===13){
            this.activeData =  this.activeProductList.kitchenData;
          }else if(page===14){
            this.activeData =  this.activeProductList.showerRoomData;
          }else if(page===15){
            this.activeData =  this.activeProductList.toiletData;
          }else if(page===16){
            this.activeData =  this.activeProductList.totoData;
          }else if(page===17){
            this.activeData =  this.activeProductList.sprinklerData;
          }else if(page===18){
            this.activeData =  this.activeProductList.coverData;
          }else if(page===19){
            this.activeData =  this.activeProductList.sensoData;
          }else if(page===20){
            this.activeData =  this.activeProductList.wallHungTypeData;
          }else if(page===21){
            this.activeData =  this.activeProductList.urinalsData;
          }
        },
        checkNav(val){
          this.addClassToBg = val;
         if(val===1){
               this.activeData =  this.activeProductList.ceramicData;
               this.$store.dispatch('listenTOAllTitle',['首页','产品中心','浴室系列','陶瓷盆']);
             }else if(val===2){
               this.activeData =  this.activeProductList.squattingData;
               this.$store.dispatch('listenTOAllTitle',['首页','产品中心','浴室系列','蹲便器']);
             }else if(val===3){
               this.activeData =  this.activeProductList.pedestalData;
               this.$store.dispatch('listenTOAllTitle',['首页','产品中心','浴室系列','立柱盆']);
             }else if(val===4){
               this.activeData =  this.activeProductList.stoolData;
               this.$store.dispatch('listenTOAllTitle',['首页','产品中心','浴室系列','马桶']);
             }else if(val===9){
               this.activeData =  this.activeProductList.showerData;
               this.$store.dispatch('listenTOAllTitle',['首页','产品中心','五金系列','花洒']);
             }else if(val===10){
               this.activeData =  this.activeProductList.faucetData;
               this.$store.dispatch('listenTOAllTitle',['首页','产品中心','五金系列','龙头']);
             }else if(val===12){
               this.activeData =  this.activeProductList.hardwareData;
               this.$store.dispatch('listenTOAllTitle',['首页','产品中心','五金系列','小五金']);
             }else if(val===13){
               this.activeData =  this.activeProductList.kitchenData;
               this.$store.dispatch('listenTOAllTitle',['首页','产品中心','定制系列','浴室柜定制']);
             }else if(val===14){
               this.activeData =  this.activeProductList.showerRoomData;
               this.$store.dispatch('listenTOAllTitle',['首页','产品中心','定制系列','浴室定制']);
             }else if(val===15){
               this.activeData =  this.activeProductList.toiletData;
               this.$store.dispatch('listenTOAllTitle',['首页','产品中心','定制系列','卫生间定制']);
             }else if(val===16){
               this.activeData =  this.activeProductList.totoData;
               this.$store.dispatch('listenTOAllTitle',['首页','产品中心','智能系列','智能马桶']);
             }else if(val===17){
               this.activeData =  this.activeProductList.sprinklerData;
               this.$store.dispatch('listenTOAllTitle',['首页','产品中心','智能系列','恒温花洒']);
             }else if(val===18){
               this.activeData =  this.activeProductList.coverData;
               this.$store.dispatch('listenTOAllTitle',['首页','产品中心','智能系列','智能马桶盖']);
             }else if(val===19){
               this.activeData =  this.activeProductList.sensoData;
               this.$store.dispatch('listenTOAllTitle',['首页','产品中心','其他','感应器']);
             }else if(val===20){
               this.activeData =  this.activeProductList.wallHungTypeData;
               this.$store.dispatch('listenTOAllTitle',['首页','产品中心','其他','挂墙式']);
             }else if(val===21){
              this.activeData =  this.activeProductList.urinalsData;
               this.$store.dispatch('listenTOAllTitle',['首页','产品中心','其他','小便器']);
             }
          },
        jumpToDetails(val){
          this.$router.push({
            path: '/details',
            query: {
              id: val
            }
          });
          $('body,html').animate({
            scrollTop:0
          },500)
        }
      }
    }
</script>

<style scoped>
  .about{
    width: 100%;
    margin:10px auto 0 auto;
    overflow: hidden;
  }
  .minWidth {
    width: 960px;
    margin: 0 auto;
  }

  .productsTitle{
    width: 100%;
    height: 40px;
    overflow: hidden;
    margin-top: 40px;
  }
  .productsText{
    width: 10%;
    font-weight: bold;
    background-color: #e5e5e5;
    text-align: center;
    float: left;
    line-height: 40px;
  }
  .navList{
    width: 90%;
    line-height: 40px;
    background-color: #fafafa;
    float: left;
    overflow: hidden;
  }
  .navList ul {
    width: 60%;
    overflow: hidden;
    display: flex;
  }
  .navList ul li{
    flex: 1;
    text-align: center;
   cursor: pointer;
    margin-right: 10px;
    font-size: 16px;
    color: #999;
  }
  .navList ul li:hover{
    color: #4fb1f7 !important;
  }
  .baColor{
       color: #4fb1f7 !important;
  }

  .proList{
    width: 100%;
    margin-top: 30px;
    overflow: hidden;
  }
  .list-item{
    width: 230px;
    float: left;
    overflow: hidden;
    border: 1px solid #f4f4f4;
    margin-right: 12px;
    margin-bottom: 20px;
    box-sizing: border-box;
    cursor: pointer;
  }
  .list-item:nth-child(4n){
    margin-right: 0;
  }
  .listImg{
    width: 100%;
    overflow: hidden;
  }
  .listImg img{
    width: 230px;
    height: 230px;
    display: block;
    transition: all .6s;
  }
  .list-item:hover img{
    transform: scale(1.4);
  }
  .list-item:hover{
    border: 1px solid saddlebrown;
    box-sizing: border-box;
  }
  .listText{
    width: 100%;
    padding: 10px 0;
    font-size: 14px;
    color: #666666;
    text-align: center;
    background-color: #f4f4f4;
  }
  .pageBottom{
    margin: 18px 0 30px 0;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  /*移动端*/
  @media screen and (max-width: 768px) {
    .about{
      width: 100%;
      margin:10px auto 0 auto;
      overflow: hidden;
    }
    .minWidth {
      width: 94%;
      margin: 0 auto;
    }
    .productsTitle{
      width: 100%;
      height: 40px;
      overflow: hidden;
      margin-top: 20px;
    }
    .productsText{
      width: 20%;
      font-weight: bold;
      font-size: 12px;
      background-color: #e5e5e5;
      text-align: center;
      float: left;
      line-height: 40px;
    }
    .navList{
      width: 80%;
      line-height: 40px;
      background-color: #fafafa;
      float: left;
      overflow: hidden;
    }
    .navList ul {
      width:100%;
      overflow: hidden;
      display: flex;
    }
    .navList ul li{
      flex: 1;
      text-align: center;
      cursor: pointer;
      margin-right: 10px;
      font-size: 12px;
      color: #999;
    }

    .proList{
      width: 100%;
      margin-top: 30px;
      overflow: hidden;
    }
    .list-item{
      width: 49%;
      float: left;
      overflow: hidden;
      border: 1px solid #f4f4f4;
      margin-right:2%;
      margin-bottom: 10px;
      box-sizing: border-box;
      cursor: pointer;
    }
    .list-item:nth-child(2n){
      margin-right: 0;
    }
    .listImg{
      width: 100%;
      height: 172px;
      overflow: hidden;
    }
    .listImg img{
      width: 100%;
      height: 100%;
      display: block;
    }
    .listText{
      width: 100%;
      padding: 10px 0;
      font-size: 12px;
      color: #666666;
      text-align: center;
      background-color: #f4f4f4;
    }
    .pageBottom{
      margin: 18px 0 30px 0;
    }
  }
</style>



